<template>
  <div id="app">
    <section class="section">
      <div class="container">
        <h1 class="title">
          Simulador de coletivo 😷🚍
        </h1> 
        <p class="subtitle">
          Simulador das medidas de mitigação ao COVID-19 dentro dos coletivos
        </p>

        <div class="columns">
          <div class="column is-paddingless">
            
            <div class="">
              <div class="field">
                
                <label v-if="!cargaHorario.aplicarCarga" for="input.qtdPassageiros">Passageiros: {{input.qtdPassageiros}}</label>
                <label v-if="cargaHorario.aplicarCarga" for="input.qtdPassageiros">Média de passageiros: {{input.qtdPassageiros}}</label>

                <div class="control">
                  <input class="has-background-dark has-text-light" v-model="input.qtdPassageiros" min="1" type="range" name="input.qtdPassageiros">
                </div>
              </div>
            </div>
            
            <div class="">
              <div class="field">
                <label for="input.comprimento">Comprimento: {{input.comprimento}} metros</label>
                <div class="control">
                  <input class="has-background-dark has-text-light" v-model="input.comprimento" min="10" max="100" type="range" name="input.comprimento">
                </div>
              </div>
            </div>
            
            <div class="">
              <div class="field">
                <label for="input.largura">Largura: {{input.largura}} metros</label>
                <div class="control">
                  <input class="has-background-dark has-text-light" v-model="input.largura" min="3" max="6" type="range" name="input.largura">
                </div>
              </div>
            </div>
            
            <div class="">
              <div class="field">
                <label for="input.largura">Fator distanciamento: {{input.distanciamento}}</label>
                <div class="control">
                  <input class="has-background-dark has-text-light" v-model="input.distanciamento" min="1" max="100" type="range" name="input.distanciamento">
                </div>
              </div>
            </div>

          </div>

          <div class="column is-paddingless">

            <div class="">
              <div class="field">
                  <label for="input.largura">Adesão ao uso da máscara: {{input.adesaoMascara}} %</label>
                  <div class="control">
                    <input class="has-background-dark has-text-light" v-model="input.adesaoMascara" min="0" max="100" type="range" name="input.adesaoMascara">
                  </div>
              </div>
            </div>
            
            <div class="">
              <div class="field">
                <label for="input.largura">Adesão ao uso do traje astronauta {{input.adesaoAstronauta}} %</label>
                <div class="control">
                  <input class="has-background-dark has-text-light" v-model="input.adesaoAstronauta" min="0" max="100" type="range" name="input.adesaoAstronauta">
                </div>
              </div>
            </div>
            
            <div class="">
              <div class="field">
                <input type="checkbox" value="Jack" v-model="cargaHorario.aplicarCarga">

                <label v-if="!cargaHorario.aplicarCarga" for="input.largura"> Aplicar curva de carga</label>

                <label for="input.largura" v-if="cargaHorario.aplicarCarga"> {{qtdPassageirosCarga}} pessoas às {{cargaHorario.horario}} hs</label>

                <div v-if="cargaHorario.aplicarCarga" class="control">
                  <input class="has-background-dark has-text-light horarioRange" v-model="cargaHorario.horario" min="0" max="23" type="range" name="horario" :disabled="!cargaHorario.aplicarCarga">
                </div>
                <div v-show="cargaHorario.aplicarCarga" class="horarioRange" style="position: relative;height: 80px;">
                  <canvas ref="cargaChart"></canvas>
                </div>
              </div>
            </div>

          </div>

        </div>

        <div class="columns _canvasfooter">
          <div class="column">
            <!-- <p>Vagão do coletivo<span class="is-size-3">🚌</span></p> -->
            <div class="xcard">
              <div class="xcard-content">
                <div class="chart-container" style="position: relative;">
                  <canvas ref="myChart" id="myChart"></canvas>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="columns">        
          <div class="column">
            <div class="card is-radiusless">
              <header class="card-header">
                <p class="card-header-title">
                  Conclusão
                </p>
              </header>
              <div class="card-content">
                <p class="is-size-5">{{ violacoesDeDistanciamento }} casos de violação do distanciamento.</p>

                <p class="is-size-5">{{ casosRiscoAlto }} passageiros com alto risco de contaminação <span class="is-size-4 has-text-danger">{{ casosRiscoAltoPct }}.</span></p>
                
                <p class="is-size-5">{{ casosRiscoBaixo }} passageiros com baixo risco de contaminação <span class="is-size-4 has-text-success">{{ casosRiscoBaixoPct }}.</span></p>
                
              </div>
            </div>
          </div>
        </div>

      </div>      
    </section>  
    <br>
    <br>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import { Chart } from 'chart.js'
import { jStat } from 'jstat'
const seedrandom = require('seedrandom')
window.jStat = jStat

const app = {
  name: 'App',
  components: {},
  data () {
    return {
      input: {
        qtdPassageiros: 18,
        largura: 5,
        comprimento: 30,
        distanciaSegura: 1,
        distanciamento: 29,
        adesaoMascara: 90,
        adesaoAstronauta: 0,
        curvaDeCarga: [0.1, 0.1, 0.2, 0.3, 0.5, 0.8, 0.9, 1, 1, 0.8, 0.7, 0.5, 0.5, 0.7, 0.9, 0.8, 0.6, 0.5, 0.4, 0.3, 0.3, 0.2, 0.1, 0.1]
      },
      cargaHorario: {
        horario: 11,
        aplicarCarga: false
      },
      emojis: [],
      seed: 123,
      veiculoChart: undefined,
      cargaChart: undefined,
      // fator que converte pixels para metros
      fatorEscala: undefined, 
      // espaço entre passageito e as paredes
      ppadd: 0.5, 
       // media da distribuição normal que descreve o tamanho do passageiro
      pRaioMean: 0.3,
      // desvio da distribuição normal que descreve o tamanho passageiro 
      pRaioStd: 0.05,
    }
  },
  watch: {
    input: {
      handler: function () {
        this.drawVeiculo()
      },
      deep: true
    },
    cargaHorario: {
      handler: function () {
        this.drawCurvaCarga()
        this.drawVeiculo()
      },
      deep: true
    }
  },
  methods: {
    setRandomSeed () {
      this.seed = Date.now()
    },
    drawVeiculo () {
      const seededFn = seedrandom(this.seed)
      jStat.setRandom(seededFn)

      var ctx = this.$refs.myChart
      
      this.veiculoChart = this.veiculoChart || new Chart(ctx, {
        type: 'bubble',
        drawTicks: false,
        data: {
          datasets: [
            {
              label: 'Passageiros',
              data: undefined,
              //pointStyle: ['rectRot'],
              pointStyle: undefined,
              borderWidth: 1
            },
            {
              label: 'Distancia',
              data: undefined,
              borderWidth: 1
            }
          ]
        },
        options: {
          tooltips: {
            enabled: false
          },
          legend: {
            display: false
          },
          scales: undefined,
          //responsive: false,
          maintainAspectRatio: false,
          //aspectRatio: 2,
          //responsiveAnimationDuration: 0,
        },
      })//veiculoChart

      
      // o raio da bolha não é plotado em pixels, portanto devemos calcular um fator
      // correspondente para converter de pixel para metro
      let chart = this.veiculoChart
      let xmarginRight = chart.scales['x-axis-0'].margins.right
      let xmarginLeft = chart.scales['x-axis-0'].margins.left

      //let ymarginRight = chart.scales['y-axis-0'].margins.right
      //let ymarginLeft = chart.scales['y-axis-0'].margins.left
      
      this.fatorEscala = (chart.chartArea.right-chart.chartArea.left)/this.input.comprimento

      chart.canvas.parentNode.style.height = (
        (this.input.largura * this.fatorEscala+xmarginRight+xmarginLeft)
      ).toFixed(2)+'px'
      
      
      // escala a imagem dos emojis para o tamanho médio das pessoas e aumenta um pouco
      const tamanhoEmoji = this.pessoasDistViolacao.map(item => item.r).reduce((a, c) => a +c, 0)/this.pessoasDistViolacao.length
      this.emojis.forEach(item => {
        item.height = tamanhoEmoji*1.1
        item.width = tamanhoEmoji*1.1
      })


      chart.data.datasets[0].data = this.pessoasAtributos
      chart.data.datasets[0].pointStyle = this.pessoasAtributos
        .map((item, i) => {
          if (item.usaAstro) return this.emojis[2+1+(i % 2)]
          return item.usaMascara ? this.emojis[0] : this.emojis[1+(i % 2)]
        })

      chart.data.datasets[1].data = this.pessoasDistViolacao
      chart.data.datasets[1].backgroundColor = this.riscoCor
      
      chart.options.scales = {
        yAxes: [{
          gridLines: {
            //drawBorder: false,
            drawTicks: false,
            display: false
          },
          ticks: {
            beginAtZero: true,
            max: parseFloat(this.input.largura),
            display: false
          }
        }],
        xAxes: [{
          gridLines: {
            //drawBorder: false,
            drawTicks: false,
            display: true
          },

          ticks: {
            beginAtZero: true,
            max: parseFloat(this.input.comprimento),
            display: false
          }
        }]
      }
      //this.nextTick(chart.update)
      chart.update()
      
    },//drawVeiculo
    drawCurvaCarga () {
      var ctx = this.$refs.cargaChart
      
      this.cargaChart = this.cargaChart || new Chart(ctx, {
        type: 'bar',
        //drawTicks: false,
        data: {
          datasets: [
            {
              label: 'carga',
              data: undefined,
              borderWidth: 0.1
            }
          ],
          labels: undefined,
        },
        options: {
          tooltips: {
            enabled: true
          },
          legend: {
            display: false
          },
          responsive: true
        },
      })
      
      let chart = this.cargaChart

      chart.options.scales = {
        yAxes: [{
          gridLines: {
            drawTicks: false,
            display: false
          },
          ticks: {
            display: false
          }
        }],
        xAxes: [{
          barPercentage: 1.0,
          categoryPercentage: 1.0,
          gridLines: {
            drawTicks: true,
            display: false
          },
          ticks: {
            display: false
          }
        }]
      }
      
      const horarios = this.input.curvaDeCarga.map((item, i) => i)

      chart.data.labels = horarios.map(item => item + ' hs')
      chart.data.datasets[0].data = this.input.curvaDeCarga
      
      if (this.cargaHorario.aplicarCarga) {
        chart.data.datasets[0].backgroundColor = horarios
          .map((item, i) => item === parseFloat(this.cargaHorario.horario) ? 'rgba(0, 117, 255, 1)' 
            : 'rgba(200, 35, 12, ' + (0.5 + parseFloat(this.input.curvaDeCarga[i])*0.6) + ')'
          )
      } else {
        chart.data.datasets[0].backgroundColor = new Array(horarios.length).fill('rgba(114, 120, 158, 1)')
      }

      chart.options.tooltips.enabled = this.cargaHorario.aplicarCarga

      chart.update()
    }
  },//methods
  mounted () {
    '😷 😒 😬 👩‍🚀 👨‍🚀'.split(' ').map((emoji, i) => {
      let canvas = document.getElementById('emojiCanvas'+i)
      let ctx = canvas.getContext('2d')
      ctx.font = '80px serif'
      ctx.textAlign = 'center';
      ctx.textBaseline = 'middle'
      ctx.fillText(emoji, canvas.width / 2, canvas.height / 2 + 5)
      var dataURL = canvas.toDataURL()
      var img = new Image()
      img.src = dataURL
      this.emojis.push(img)
    })    

    this.drawVeiculo()
    this.drawCurvaCarga()
    ;(function() {
        var throttle = function(type, name, obj) {
            obj = obj || window;
            var running = false;
            var func = function() {
                if (running) { return; }
                running = true;
                 requestAnimationFrame(function() {
                    obj.dispatchEvent(new CustomEvent(name));
                    running = false;
                });
            };
            obj.addEventListener(type, func);
        };
        throttle("resize", "optimizedResize");
    })();
    window.addEventListener("optimizedResize", this.drawVeiculo)
    //window.addEventListener('resize', this.$nextTick(this.drawVeiculo)) 
    this.$nextTick(this.drawVeiculo)
  },
  computed: {
    opcoesLugares () {
      return new Array(parseInt(this.qtdPassageirosCarga))
        .fill(0)
        .map((_, id) => Array(parseFloat(this.input.distanciamento)).fill(Object({id: id})).map(item => 
          Object({
            id: item.id,
            x: jStat.uniform.sample(
              0 + this.ppadd, this.input.comprimento - this.ppadd),
            y: jStat.uniform.sample(
              0 + this.ppadd, this.input.largura - this.ppadd),
            rp: jStat.normal.sample(this.pRaioMean, this.pRaioStd) * this.fatorEscala
            // x: 4,
            // y: 2,
            // rp: 1 * this.fatorEscala
          })
        )
      )//.sort((a, b) => Math.sqrt((a.x - b.x)*(a.x - b.x) + (a.y - b.y)*(a.y - b.y)))
    },//opcoesLugares
    pessoas () {
      let pessoasPosicionadas = []
      return this.opcoesLugares.map(item => {      

        let scores = item.map((x) => {
          return [x, (pessoasPosicionadas.map((pessoasPosicionada) => {
            let dx = Math.abs(pessoasPosicionada.x - x.x)
            let dy = Math.abs(pessoasPosicionada.y - x.y)
            let dist = Math.sqrt(dx*dx + dy*dy)
            return 1/dist// - 1/Math.abs(item[0].x - x.x)
          }).reduce((a, c) => c > a ? c : a , 0))]
        })
        
        let itemMelhorScore = scores.sort((a, b) => a[1]-b[1])[0][0]
        pessoasPosicionadas.push(itemMelhorScore)
        return itemMelhorScore
      })
    },
    pessoasDist () {
      return this.pessoas.map((item, i) => {
        const seededFn = seedrandom(i)
        jStat.setRandom(seededFn)
        return Object({
          id: item.id,
          x: item.x,
          y: item.y,
          rp: item.rp,
          r: ((item.rp/this.fatorEscala) + this.input.distanciaSegura) * this.fatorEscala,
          usaMascara: jStat.uniform.sample(0, 100) > (100 - parseFloat(this.input.adesaoMascara)),
          usaAstro: jStat.uniform.sample(0, 100) > (100 - parseFloat(this.input.adesaoAstronauta))
        })
      })
    },
    pessoasDistViolacao () {
      return this.pessoasDist.map(item => {
        let res = Object({
          x: item.x,
          y: item.y,
          rp: item.rp,
          r: item.r,
          usaMascara: item.usaMascara,
          usaAstro: item.usaAstro,
          violacoes: this.pessoasDist.reduce((a, c) => {
            if (c.id === item.id) return a
            let dx = Math.abs(item.x - c.x)
            let dy = Math.abs(item.y - c.y)
            let dist = Math.sqrt(dx*dx + dy*dy)
            if (dist < (item.r/this.fatorEscala + c.r/this.fatorEscala)) {
              a.push(c)
            }
            return a
          }, [])
        })
        res.violada = !!res.violacoes.length
        return res
      })
    },
    pessoasAtributos () {
      return this.pessoasDistViolacao.map(item => {
        return Object({
          x: item.x,
          y: item.y,
          r: item.rp,
          usaMascara: item.usaMascara,
          usaAstro: item.usaAstro
        })
      })
    },
    violacoesDeDistanciamento () {
      return this.pessoasDistViolacao.filter(item => item.violada).length
    },
    riscoContaminacaoPassageiros () {
      //console.log(this.pessoasAtributos.length, this.pessoasDistViolacao.length)
      const scoreFatores = {
        umComOutroCom: 2,
        umComOutroSem: 5,
        umSemOutroCom: 4,
        umSemOutroSem: 10,

        umComOutroComAtro: -1000,
        umComOutroSemAtro: -1000,
        umSemOutroComAtro: 0,
        umSemOutroSemAtro: 0,
      }
      
      return this.pessoasAtributos.map((passageiro, i) => {
        let distViolacao = this.pessoasDistViolacao[i]

        let risco = 0

        if (passageiro.usaMascara) {
          risco = risco + distViolacao.violacoes
            .reduce((a, c) => a + (c.usaMascara ? scoreFatores.umComOutroCom : scoreFatores.umComOutroSem), 0)
        } else {
          risco = risco + distViolacao.violacoes
            .reduce((a, c) => a + (c.usaMascara ? scoreFatores.umSemOutroCom : scoreFatores.umSemOutroSem), 0)          
        }

        if (passageiro.usaAstro) {
          risco = risco + distViolacao.violacoes
            .reduce((a, c) => a + (c.usaAstro ? scoreFatores.umComOutroComAtro : scoreFatores.umComOutroSemAtro), 0)
        } else {
          risco = risco + distViolacao.violacoes
            .reduce((a, c) => a + (c.usaAstro ? scoreFatores.umSemOutroComAtro : scoreFatores.umSemOutroSemAtro), 0)          
        }

        return risco
      })
    },
    riscoBool () {
      const riscoThreshold = 3
      return this.riscoContaminacaoPassageiros
        .map(item => item > riscoThreshold)
    },
    riscoCor () {
      return this.riscoBool
        .map(item => item ? 'rgba(255, 99, 132, 0.4)' : 'rgba(10, 200, 50, 0.4)')
    },
    casosRiscoAlto () {
      return this.riscoBool.filter(item => item).length
    },
    casosRiscoAltoPct () {
      return (this.casosRiscoAlto/this.riscoBool.length*100).toFixed(0)+ ' %'
    },
    casosRiscoBaixo () {
      return this.riscoBool.length - this.casosRiscoAlto
    },
    casosRiscoBaixoPct () {
      return (this.casosRiscoBaixo/this.riscoBool.length*100).toFixed(0)+ ' %'
    },
    cargaHorarioSel () {
      return this.input.curvaDeCarga[this.cargaHorario.horario]
    },
    qtdPassageirosCarga () {
      if (!this.cargaHorario.aplicarCarga) return this.input.qtdPassageiros
      let cargaSel = this.input.curvaDeCarga[this.cargaHorario.horario]
      return Math.round(parseFloat(this.input.qtdPassageiros) * cargaSel)
    }
  }
}

export default app

</script>
