    #676e9a 2px,
    #676e9a 4px
  );

}

.input {
  max-width: 100px !important;
}

.horarioRange {
  width: 100%;
  max-width: 200px;
}

.canvasfooter {
  position: fixed;
  bottom: 10px;
  width: 90vw;
  height: 16vw;
